@import url("https://fonts.googleapis.com/css2?family=Overpass:ital,wght@0,100;0,200;0,300;0,400;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,600;1,700;1,800;1,900&display=swap");

.allDrops {
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: auto auto auto auto auto;
  gap: 0px 0px;
  grid-template-areas:
    "dropName"
    "dropProjStats"
    "dropOpp"
    "dropLastFive"
    "dropButtons";
}

.dropName {
  grid-area: dropName;
  text-align: center;
  font-family: Overpass;
  font-style: normal;
  font-weight: 800;
  font-size: 16px;
  color: black;
  height: 21px;
}

.dropProjStats {
  display: grid;
  grid-template-columns: auto auto auto auto auto;
  grid-template-rows: auto auto auto;
  gap: 0px 0px;
  grid-template-areas:
    "projStatsInfo projStatsInfo projStatsInfo projStatsInfo projStatsInfo"
    "emptyProjLabel attProjLabel ydsProjLabel tdsProjLabel ptsProjLabel"
    "weekProjInfo attProjInfo ydsProjInfo tdsProjInfo ptsProjInfo";
  grid-area: dropProjStats;
  margin-right: 10px;
  margin-left: 10px;
}

.projStatsInfo {
  grid-area: projStatsInfo;
  font-family: Overpass;
  font-style: normal;
  font-weight: 800;
  font-size: 18px;
}

.emptyProjLabel {
  grid-area: emptyProjLabel;
  background-color: #4dd9ff;
}

.attProjLabel {
  grid-area: attProjLabel;
  text-align: right;
  background-color: #4dd9ff;
  color: white;
  font-family: Overpass Mono;
  font-style: normal;
  font-weight: normal;
  font-size: 10px;
  line-height: 15px;
}

.ydsProjLabel {
  grid-area: ydsProjLabel;
  text-align: right;
  background-color: #4dd9ff;
  color: white;
  font-family: Overpass Mono;
  font-style: normal;
  font-weight: normal;
  font-size: 10px;
  line-height: 15px;
}

.tdsProjLabel {
