  color: white;
  font-family: Overpass Mono;
  font-style: normal;
  font-weight: normal;
  font-size: 10px;
  line-height: 15px;
}

.ptsProjLabel {
  grid-area: ptsProjLabel;
  text-align: right;
  background-color: #4dd9ff;
  color: white;
  font-family: Overpass Mono;
  font-style: normal;
  font-weight: normal;
  font-size: 10px;
  line-height: 15px;
  padding-right: 5px;
}

.weekProjInfo {
  grid-area: weekProjInfo;
  font-family: Overpass;
  font-style: normal;
  font-weight: 900;
  font-size: 14px;
}

.attProjInfo {
  grid-area: attProjInfo;
  text-align: right;
  font-family: Overpass Mono;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
}

.ydsProjInfo {
  grid-area: ydsProjInfo;
  text-align: right;
  font-family: Overpass Mono;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
}

.tdsProjInfo {
  grid-area: tdsProjInfo;
  text-align: right;
  font-family: Overpass Mono;
