  grid-template-rows: auto auto;
  gap: 0px 0px;
  grid-template-areas:
    "oppProjLabel homeProjLabel posRankProjLabel overallProjLabel weatherProjLabel"
    "oppNameInfo homeInfo posRankInfo ovrRankInfo weatherInfo";
  grid-area: dropOpp;
  margin-right: 10px;
  margin-left: 10px;
}

.oppProjLabel {
