.dropLastFive {
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: auto auto;
  gap: 0px 0px;
  grid-template-areas:
    "lastFiveLabel"
    "lastFiveGraph";
  grid-area: dropLastFive;
  margin-left: 10px;
  margin-right: 10px;
}

.lastFiveLabel {
  grid-area: lastFiveLabel;
  font-family: Overpass;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  margin-top: 5px;
  margin-bottom: 10px;
}

.lastFiveGraph {
  grid-area: lastFiveGraph;
  place-self: center;
  border-left: 6px solid black;
  border-bottom: 6px solid black;
  border-bottom-left-radius: 50px;
  margin-bottom: 10px;
}

.dropButtons {
  display: grid;
  grid-template-columns: 35% 30% 35%;
  grid-template-rows: auto;
  gap: 0px 0px;
  grid-template-areas: "watchListDropButton chatDropButton moreInfoDropButton";
  grid-area: dropButtons;
  text-align: center;
  margin-right: 10px;
  margin-left: 10px;
  margin-bottom: 10px;
}

.watchListDropButton {
  grid-area: watchListDropButton;
  font-family: Overpass;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
}

.chatDropButton {
  grid-area: chatDropButton;
  font-family: Overpass;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
}

.moreInfoDropButton {
  grid-area: moreInfoDropButton;
  font-family: Overpass;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  margin-top: 6px;
}

.watchListDropButton img {
  width: 30px;
  height: 30px;
}
.chatDropButton img {
  margin-top: 2px;
  width: 30px;
  height: 30px;
}
.moreInfoButton {
  margin-top: 4px;
  font-family: Overpass;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  border-radius: 10px;
  background-color: #4dd9ff;
  color: white;
  border-color: #4dd9ff;
  z-index: 4;
  padding-right: 5px;
  padding-left: 5px;
  padding-top: 4px;
  padding-bottom: 2px;
}
