.clearfix::after {
  content: "";
  display: block;
  clear: both;
}
#allChatRoom aside {
  display: none;
}
#myChart {
  background-color: white;
}
#bottomChatAds {
  width: 100vw;
  text-align: center;
  margin-top: -150px;
}
#leftChatAds {
  position: absolute;
  width: 12vw;
  height: 560px;
  top: 122px;
}
#rightChatAds {
  position: absolute;
  height: 560px;
  width: 12vw;
  top: 122px;
  right: 0;
}
