@media screen and (max-width: 1300px) {
  #leftChatAds {
    width: 100vw;
    margin-bottom: -90px;
    text-align: center;
    margin-top: 60px;
    position: unset;
    height: unset;
    display: none;
  }
  #rightChatAds {
    position: unset;
    width: 100vw;
    text-align: center;
    height: unset;
    margin-top: -150px;
    display: none;
  }
}
@keyframes loading {
  0%,
  80%,
  100% {
    box-shadow: 0 1em 0 -1.3em;
  }
  40% {
    box-shadow: 0 1em 0 0;
  }
}

@keyframes fadeNewMessage {
  0% {
    opacity: 0;
    transform: translateY(1rem);
  }
  100% {
    opacity: 1;
    transform: translateY(0px);
  }
}
@keyframes loadSendMessage {
  0% {
    opacity: 1;
    width: 4px;
    height: 4px;
  }
  100% {
    opacity: 0;
    width: 100%;
    height: 100%;
  }
}
@keyframes typingMessage {
  0% {
    background-color: #606060;
  }
  50% {
    background-color: #fff;
  }
  60% {
    background-color: #606060;
  }
}
