}

@font-face {
  font-family: "AKZIDENZGROTESK-MEDIUM";
  src: local("font"),
    url("../Fonts/AKZIDENZGROTESK-MEDIUM.OTF") format("opentype");
}

@font-face {
  font-family: "AKZIDENZGROTESK-BOLD_0";
  src: local("font"),
    url("../Fonts/AKZIDENZGROTESK-BOLD_0.OTF") format("opentype");
}

@font-face {
  font-family: "AKZIDENZGROTESK-BOLDITALIC";
  src: local("font"),
    url("../Fonts/AKZIDENZGROTESK-BOLDITALIC.OTF") format("opentype");
