.loading-bar {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  height: 3px;
  width: 300px;
  background-color: #a9d6fa;
  overflow: hidden;
}

.loading-bar:after {
  content: " ";
  animation: load 1s infinite;
  position: absolute;
  width: 20%;
  background-color: #40a3f6;
  height: 100%;
}

@keyframes load {
  from {
    left: -40%;
  }
  50% {
    left: 100%;
  }
  to {
    left: -40%;
  }
}

@keyframes load {
  from {
    left: -40%;
  }

  50% {
    left: 100%;
  }

  to {
    left: -40%;
  }
}

#content-wrapper {
  color: #fff;
  position: fixed;
  left: 0;
  top: 20px;
  width: 100%;
  height: 100%;
}

#header {
  width: 800px;
  margin: 0 auto;
  text-align: center;
  height: 100px;
  background-color: #666;
}

#content {
  width: 800px;
  height: 1000px;
  margin: 0 auto;
  text-align: center;
  background-color: #888;
}

.navLink button {
  background-color: transparent;
  border: transparent;
  position: absolute;
  right: 43px;
  width: 40px;
  height: 22px;
}

.mainBody {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0px;
  z-index: -1;
}

.heroImage {
  object-fit: scale-down;
  top: 0%;
  width: 100%;
  height: 80%;
  position: absolute;
  z-index: 1;
  overflow: hidden;
}

.heroImage img {
  position: absolute;
  display: block;
  max-width: 100%;
  top: 0;
  left: 0;
  width: 100%;
}

.newsContent {
  width: 77%;
  margin-left: auto;
  margin-right: auto;
  padding-top: 5px;
  padding-bottom: 5px;
  background-color: #ffffff;
  display: block;
  position: relative;
  top: 50%;
  box-shadow: 15px 0px 20px #222222, -15px 0px 20px #222222;
  z-index: 1;
  margin-top: 315px;
}

.heroHeader {
  font-size: 48px;
  font-family: "AKZIDENZGROTESK-BOLDEXTENDED", Tahoma, sans-serif;
  line-height: 90%;
  margin-top: 3%;
  margin-bottom: 12px;
  margin-right: 10%;
  margin-left: 10%;
  display: inline-block;
}

.heroSubtitle {
  font-size: 30px;
  font-family: "AKZIDENZGROTESK-BOLD_0", Tahoma, sans-serif;
  margin-right: 10%;
  margin-left: 10%;
  display: inline-block;
}
.newsBodyIndent {
  text-indent: 20px;
}
.title {
  font-size: 40px;
  font-family: "AKZIDENZGROTESK-BOLD_0", Tahoma, sans-serif;
  text-align: center;
  margin-top: 25px;
  margin-bottom: 12px;
  padding-left: 10%;
  padding-right: 10%;
  margin-left: auto;
  margin-right: auto;
  display: block;
}

.newsHeader {
  font-size: 40px;
  font-family: "AKZIDENZGROTESK-BOLD_0", Tahoma, sans-serif;
  margin-top: 25px;
  margin-bottom: 12px;
  padding-left: 10%;
  padding-right: 10%;
  margin-left: auto;
  margin-right: auto;
  display: block;
}

.sectionImage {
  object-fit: scale-down;
  overflow: hidden;
  position: relative;
  width: 100%;
  height: 375px;
  display: block;
  margin-bottom: 30px;
}

.sectionImage img {
  position: absolute;
  width: 100%;
  display: block;
  top: 0;
  left: 0;
}

.newsBody {
  font-size: 16px;
  font-family: "AKZIDENZGROTESK-MEDIUM", Tahoma, sans-serif;
  margin-bottom: 12px;
  margin-right: 10%;
  margin-left: 10%;
  display: inline-block;
}

.newsSubheader {
  font-size: 32px;
  font-family: "AKZIDENZGROTESK-BOLD_0", Tahoma, sans-serif;
  margin-top: 5px;
  margin-bottom: 15px;
  margin-right: 10%;
  margin-left: 10%;
  display: block;
}

.newsBodyEmphasis {
  font-size: 20px;
  font-family: "AKZIDENZGROTESK-BOLD_0", Tahoma, sans-serif;
  margin-top: 5px;
  margin-bottom: 15px;
  margin-right: 10%;
  margin-left: 10%;
  display: inline-block;
}

.friendFoe {
  display: inline-block;
  margin-right: 10%;
  margin-left: 10%;
  margin-bottom: 15px;
}

.emphasis {
  font-size: 20px;
  font-family: "AKZIDENZGROTESK-BOLD_0", Tahoma, sans-serif;
  margin-top: 5px;
  margin-bottom: 15px;
}

.friendFoe p {
  font-size: 16px;
  font-family: "AKZIDENZGROTESK-MEDIUM", Tahoma, sans-serif;
  margin-bottom: 12px;
}

.bodyImageRightTop {
  overflow: hidden;
  width: 200px;
  height: 200px;
  object-fit: cover;
  float: right;
  margin-left: 5%;
  margin-right: 10%;
  vertical-align: top;
}

.bodyImageLeftTop {
  width: 150px;
  height: 150px;
  object-fit: cover;
  float: left;
  margin-right: 2%;
  margin-bottom: 4%;
  vertical-align: top;
}

.bodyImageRightMiddle {
  overflow: hidden;
  width: 200px;
  height: 200px;
  object-fit: cover;
  float: right;
  margin-left: 5%;
  margin-right: 10%;
  vertical-align: middle;
}

.bodyImageLeftMiddle {
  overflow: hidden;
  width: 200px;
  height: 200px;
  object-fit: cover;
  float: left;
  margin-left: 10%;
  margin-right: 5%;
  vertical-align: middle;
}

.column {
  float: left;
  width: 50%;
}

.singleColumn {
  font-size: 20px;
  font-family: "AKZIDENZGROTESK-BOLD_0", Tahoma, sans-serif;
  text-align: center;
  margin-bottom: 12px;
  padding-left: 10%;
  padding-right: 10%;
  margin-top: 15px;
  margin-left: auto;
  margin-right: auto;
  display: block;
}

ol {
  display: inline-block;
  text-align: left;
}

#inOut ul {
  padding: 0;
  list-style-type: none;
}

.row:after {
  content: "";
  display: table;
  clear: both;
}

.arrowLeft {
  object-fit: scale-down;
  overflow: hidden;
  position: relative;
  width: 50px;
  height: 50px;
  margin-left: 10%;
  margin-bottom: 25px;
  margin-top: -15px;
  display: block;
}

.arrowCenter {
  object-fit: scale-down;
  overflow: hidden;
  position: relative;
  width: 50px;
  height: 50px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 25px;
  display: block;
}

.arrowSOS {
  object-fit: scale-down;
  overflow: hidden;
  position: relative;
  width: 50px;
  height: 50px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 25px;
  display: block;
}

.arrowLeft img {
  position: absolute;
  max-width: 100%;
  display: block;
  top: 0;
  left: 0;
}

.arrowCenter img {
  position: absolute;
  max-width: 100%;
  display: block;
  top: 0;
  left: 0;
}

.arrowSOS img {
  position: absolute;
  max-width: 100%;
  display: block;
  top: 0;
  left: 0;
}

.logoCenter {
  object-fit: scale-down;
  overflow: hidden;
  position: relative;
  width: 80px;
  height: 165px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 40px;
  margin-bottom: -109px;
  display: block;
}

.logoCenter img {
  position: absolute;
  max-width: 100%;
  display: block;
  top: 0;
  left: 0;
}

#inOutMobile {
  display: none;
}

.ChartParagraph {
  display: inline-block;
  margin-right: 10%;
  margin-bottom: 15px;
}

.ChartParagraph p {
  font-size: 16px;
  font-family: "AKZIDENZGROTESK-MEDIUM", Tahoma, sans-serif;
  margin-bottom: 12px;
}

.verticalchart {
  width: 150px;
}

.chartImageNarrow {
  width: 162.5px;
  height: 100%;
  object-fit: cover;
  float: left;
  margin-left: 10%;
  margin-right: 5%;
  vertical-align: top;
}

@media screen and (max-width: 900px) {
  .heroImage {
    object-fit: cover;
    top: 5%;
    width: 100%;
    height: 150%;
    position: absolute;
    z-index: 1;
    overflow: hidden;
  }

  .heroImage img {
    max-width: 200%;
    top: 0;
    left: -50%;
    width: unset;
  }

  .newsContent {
    width: 100%;
  }

  .sectionImage {
    height: 250px;
  }

  .friendFoe {
    margin-left: 10%;
  }

  .friendFoe img {
    display: none;
  }

  .friendFoe p {
    margin-left: 10%;
  }

  #inOutDesktop {
    display: none;
  }

  #inOutMobile {
    display: inline-block;
  }

  .arrowSOS {
    display: none;
  }

  .ChartParagraph p {
    margin-left: 10%;
  }

  .chartImageNarrow {
    float: none;
    display: block;
    margin-left: auto;
    margin-right: auto;
  }

  .ChartParagraph .newsSubheader {
    display: block;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
  }
}

@media screen and (max-width: 425px) {
  .newsContent {
    top: 45%;
  }

  .sectionImage {
    height: 150px;
  }
}

@media screen and (max-width: 375px) {
  .newsContent {
    margin-top: 240px;
  }
  .heroHeader {
    font-size: 40px;
  }
}
