.loading-bar {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  height: 3px;
  width: 300px;
  background-color: #a9d6fa;
  overflow: hidden;
}

.loading-bar:after {
  content: " ";
  -webkit-animation: load 1s infinite;
          animation: load 1s infinite;
  position: absolute;
  width: 20%;
  background-color: #40a3f6;
  height: 100%;
}

@-webkit-keyframes load {
  from {
    left: -40%;
  }
  50% {
    left: 100%;
  }
  to {
    left: -40%;
  }
}

@keyframes load {
  from {
    left: -40%;
  }
  50% {
    left: 100%;
  }
  to {
    left: -40%;
  }
}

