  list-style-type: none;
  padding: 0;
}

.carousel__slide-item-img-link {
  text-decoration: none;
  color: inherit;
}

.carousel__wrap {
  align-items: center;
  display: flex;
  flex: 1 1;
  justify-content: center;
  margin-top: 0rem;
  position: relative;
  width: 100%;
  margin-bottom: 10rem;
}

.carousel__inner {
  height: 40rem;
  position: relative;
  width: 70rem;
}

.carousel__container {
  height: 100%;
  overflow: hidden;
  position: relative;
  width: 100%;
}

.carousel__slide-list {
  height: 100%;
  left: 50%;
  list-style-type: none;
  margin: 0;
  padding: 0;
  position: absolute;
  transform: translateX(-50%);
  width: calc(5 * 30rem);
}

.carousel__slide-item {
  height: 15rem;
  margin: 0;
  position: absolute;
  top: 2rem;
  transition: all 0.3s;
  width: 28rem;
}

.carousel__slide-item-img-link {
  cursor: zoom-in;
  display: flex;
  height: 25rem;
  height: 100%;
  overflow: hidden;
  width: 100%;
}
.carousel__slide-item-img-link img {
  height: 100%;
  object-fit: cover;
  width: 100%;
}
.carousel__slide-item-img-link::after {
  align-items: center;
  background: rgba(0, 0, 0, 0.5);
  color: white;
  content: "read more";
  display: flex;
  height: 100%;
  justify-content: center;
  opacity: 0;
  position: absolute;
  transition: all 0.5s ease;
  width: 100%;
}
.carousel__slide-item-img-link:hover::after {
  opacity: 1;
}
.carousel__slide-item-img-link:hover img {
  transform: scale(1.3);
}
.carousel__slide-item-img-link img {
  transition: all 0.5s ease;
}

.carousel-slide-item__body {
  bottom: -2.5rem;
  height: 10%;
  position: absolute;
}
.carousel-slide-item__body h4 {
  margin: 0.7rem 0 0;
  text-transform: uppercase;
}
.carousel-slide-item__body p {
  font-size: 1.2rem;
  line-height: 1.3;
  margin: 0.7rem 0 0;
}

.carousel__btn {
  align-items: center;
  background: 0;
  border: 0;
  cursor: pointer;
  display: flex;
  justify-content: center;
  position: absolute;
  top: 23%;
  transform: translateY(-50%);
  z-index: 1;
  background-color: white;
  height: 100px;
  width: 100px;
  border-radius: 30px;
}
.carousel__btn--prev {
  left: 1rem;
}
.carousel__btn--next {
  right: 1rem;
}

.carousel__btn-arrow {
  border: solid black;
  border-width: 0 0.4rem 0.4rem 0;
  height: 3rem;
  width: 3rem;
  padding: 3px;
  z-index: 10;
}
.carousel__btn-arrow--left {
  transform: rotate(135deg);
}
.carousel__btn-arrow--right {
  transform: rotate(-45deg);
}

.carousel__dots {
  display: inline-block;
  left: 50%;
  margin-top: 2rem;
  position: absolute;
  transform: translateX(-50%);
}
.carousel__dots .dot {
  background: #ccc;
  border: 0;
  border-radius: 50%;
  height: 1rem;
  margin: 0 0.3rem;
  outline: none;
  transform: scale(0.5);
  width: 1rem;
}
.carousel__dots .dot.active {
  background: black;
}
@media screen and (max-width: 800px) {
  .carousel-slide-item__body p {
    font-size: 0.8rem;
    line-height: 1.3;
    margin: 0.7rem 70px 0;
  }
  .carousel-slide-item__body h4 {
    margin: 0.7rem 70px 0;
    text-transform: uppercase;
  }
  .carousel__btn-arrow {
    height: 1rem;
    width: 1rem;
  }
  .carousel__btn {
    height: 50px;
    width: 50px;
    border-radius: 5px;
  }

  .carousel__dots {
    margin-top: -6rem;
  }
}

