@import url("https://fonts.googleapis.com/css2?family=Overpass:ital,wght@0,100;0,200;0,300;0,400;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,600;1,700;1,800;1,900&display=swap");

.playerPage {
  width: 100vw;
  margin-top: 35px;
  background-color: white;
  padding-top: 30px;
  padding-bottom: 100px;
}
.all-player-info {
  display: grid;
  grid-template-columns: 100%;
  grid-template-rows: 100%;
  gap: 0px 0px;
  grid-template-areas: "one-player";
  background-color: white;
}

.one-player {
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: auto auto auto auto auto;
  gap: 30px 0px;
  grid-template-areas:
    "name-and-info"
    "pos-info"
    "gamelogs"
    "player-outlook"
    "trends-projections";
  width: 358px;
  margin: auto;
  grid-area: one-player;
}

.name-and-info {
  display: grid;
  grid-template-columns: auto auto auto;
  grid-template-rows: auto;
  gap: 0px 0px;
  grid-template-areas: "player-image name-status chat-button";
  grid-area: name-and-info;
}
.chat-button {
  grid-area: chat-button;
}
.chat-button img {
  height: 29px;
  width: 29px;
  margin-top: 40px;
}
.player-image {
  grid-area: player-image;
  text-align: center;
  width: 121px;
  height: 121px;
}
.player-image img {
  width: 83px;
  height: 115px;
  border-radius: 35px;
  margin-top: -23px;
  margin-left: 2px;
}
.onePlayerFirstCircle {
  background-color: #4dd9ff;
  width: 100px;
  height: 100px;
  border-radius: 52px;
  margin: auto;
  -webkit-writing-mode: vertical-lr;
}
.onePlayerSecondCircle {
  background-color: white;
  width: 84px;
  height: 84px;
  border-radius: 52px;
  margin: auto;
  margin-left: 8px;
}

.name-status {
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: auto auto auto;
  gap: 0px 0px;
  grid-template-areas:
    "player-name"
    "player-team"
    "player-status";
  grid-area: name-status;
}

.player-name {
  grid-area: player-name;

  font-family: Overpass;
  font-style: normal;
  font-weight: 800;
  font-size: 18px;
  line-height: 28px;
  margin-left: -45px;
  margin-top: 20px;
}

.player-team {
  grid-area: player-team;
  font-family: Overpass;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 21px;
  margin-left: -45px;
  margin-top: -27px;
}

.player-status {
  grid-area: player-status;
  font-family: Overpass;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 21px;
  margin-left: -45px;
  margin-top: -29px;
}
.pos-info {
  display: grid;
  grid-template-columns: auto auto auto;
  grid-template-rows: auto auto;
  gap: 0px 0px;
  grid-template-areas:
    "pos-rank-title avg-pts-title rost-pct-titl"
    "pos-rank avg-pts rost-pct";
  grid-area: pos-info;
  margin-top: -35px;
}
.pos-rank-title {
  grid-area: pos-rank-title;
  font-family: Overpass;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 21px;
  text-align: center;
}
.avg-pts-title {
  grid-area: avg-pts-title;
  font-family: Overpass;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 21px;
  text-align: center;
}
.rost-pct-titl {
  grid-area: rost-pct-titl;
  font-family: Overpass;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 21px;
  text-align: center;
}
.pos-rank {
  grid-area: pos-rank;
  font-family: Overpass;
  font-style: normal;
  font-weight: 900;
  font-size: 25px;
  line-height: 38px;
  text-align: center;
}
.avg-pts {
  grid-area: avg-pts;
  font-family: Overpass;
  font-style: normal;
  font-weight: 900;
  font-size: 25px;
  line-height: 38px;
  text-align: center;
}
.rost-pct {
  grid-area: rost-pct;
  font-family: Overpass;
  font-style: normal;
  font-weight: 900;
  font-size: 25px;
  line-height: 38px;
  text-align: center;
}

.gamelogs {
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: auto auto auto;
  gap: 0px 0px;
  grid-template-areas:
    "game-log-title"
    "game-log-info"
    "game-log-stats";
  grid-area: gamelogs;
}
.game-log-title {
  grid-area: game-log-title;
  font-family: Overpass;
  font-style: normal;
  font-weight: 800;
  font-size: 18px;
  line-height: 28px;
}
.game-log-info {
  display: grid;
  grid-template-columns: auto auto auto auto auto;
  grid-template-rows: auto;
  gap: 0px 0px;
  grid-template-areas: "g-glog-empty g-log-att g-log-yds g-log-tds g-log-pts";
  grid-area: game-log-info;
  height: 20px;
  background: #4dd9ff;
  color: white;
}
.g-glog-empty {
  grid-area: g-glog-empty;
  width: 53px;
}
.g-log-att {
  grid-area: g-log-att;
  font-family: Overpass Mono;
  font-style: normal;
  font-weight: normal;
  font-size: 10px;
  line-height: 15px;
  text-align: right;
}
.g-log-yds {
  grid-area: g-log-yds;
  font-family: Overpass Mono;
  font-style: normal;
  font-weight: normal;
  font-size: 10px;
  line-height: 15px;
  text-align: right;
}
.g-log-tds {
  grid-area: g-log-tds;
  font-family: Overpass Mono;
  font-style: normal;
  font-weight: normal;
  font-size: 10px;
  line-height: 15px;
  text-align: right;
}
.g-log-pts {
  grid-area: g-log-pts;
  font-family: Overpass Mono;
  font-style: normal;
  font-weight: normal;
  font-size: 10px;
  line-height: 15px;
  text-align: right;
  padding-right: 8px;
}
.game-log-stats {
  grid-area: game-log-stats;
  font-family: Overpass Mono;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 21px;
  text-align: right;
}
.g-list-stats {
  display: inline-flex;
  float: left;
  width: 358px;
  margin-left: -40px;
}
.g-log-stats {
  font-family: Overpass Mono;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 21px;
  text-align: right;
  width: 64px;
}
.g-log-week {
  margin-left: 0px !important;
  width: 97px;
  float: left;
  text-align: left;
}

.player-outlook {
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: auto auto;
  gap: 0px 0px;
  grid-template-areas:
    "outlook-title"
    "outlook-description";
  grid-area: player-outlook;
}

.outlook-title {
  grid-area: outlook-title;
  font-family: Overpass;
  font-style: normal;
  font-weight: 800;
  font-size: 18px;
  line-height: 28px;
}

.outlook-description {
  grid-area: outlook-description;
  font-family: Overpass Mono;
  font-style: normal;
  font-weight: bold;
  font-size: 10px;
  line-height: 15px;
}

.trends-projections {
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: auto auto auto;
  gap: 0px 0px;
  grid-template-areas:
    "trends-title"
    "trends-proj-points"
    "trends-graph";
  grid-area: trends-projections;
}

.trends-title {
  grid-area: trends-title;
  font-family: Overpass;
  font-style: normal;
  font-weight: 800;
  font-size: 18px;
  line-height: 28px;
}

.trends-proj-points {
  grid-area: trends-proj-points;
  font-family: Overpass;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 21px;
  text-align: center;
}

.trends-graph {
  grid-area: trends-graph;
  border-left: 6px solid black;
  border-bottom: 6px solid black;
  margin-left: -5px;
  border-bottom-left-radius: 50px;
}
