.card-masonry {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin-top: 75px;
  align-items: center;
  justify-content: center;
  margin-left: -2rem;
}

.card-container {
  display: flex;
  flex-direction: column;
  width: 250px;
  /* margin: 5%; */
  margin-bottom: 50px;
  list-style-type: none;
  border: 1px solid #eeeeee;
  border-radius: 250px 250px 4px 4px;
  background: #ffffff;
  box-shadow: 2px 2px 10px #eae9e9;
}

.card-image {
  /* border-radius: 4px 4px 0 0; */
  object-fit: cover;
  border-radius: 100px;
  max-width: 100%;
  height: 250px;
  order: 1;
}

.card-container--content {
  display: flex;
  font-size: 12px;
  flex-direction: column;
  order: 2;
  padding: 0 2rem 2rem;
}
.card-main--text {
  font-size: 12px;
  line-height: 1.5;
}

.card-header--title {
  color: darkcyan;
  font-size: 1.5rem;
}

.card-date {
  color: #767676;
  font-size: 0.875rem;
  font-style: italic;
  margin-bottom: 0.125rem;
}

#about-sign {
  top: 70px;
  width: 100vw;
  height: 170px;
  text-align: -webkit-center;
  text-align: center;
  margin-top: 140px;
}
#about-sign img {
  width: 250px;
}
.felix-team {
  font-family: "AKZIDENZGROTESK-BOLD_0", Tahoma, Sans-Serif;
  text-align: -webkit-center;
  text-align: center;
  margin-top: 25px;
  font-size: 55px;
}
