@import url("https://fonts.googleapis.com/css2?family=Overpass:ital,wght@0,100;0,200;0,300;0,400;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,600;1,700;1,800;1,900&display=swap");

.navLogoArrow {
  height: 34px;
}
.topBar {
  height: 45px;
  background-color: #4dd9ff;
  width: 100vw;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 5;
}
.leftDirection {
  position: fixed;
  top: 5px;
  left: 12px;
  z-index: 20;
}
.pageTitle {
  z-index: 10;
  color: white;
  width: 100vw;
  top: -6px;
  position: fixed;
  text-align: center;
  font-family: Overpass;
  font-style: normal;
  font-weight: 800;
  font-size: 18px;
  line-height: 28px;
}
.rankPositions {
  position: fixed;
  top: 45px;
  height: 65px;
  width: 100vw;
  left: 0;
  text-align: center;
  background-color: white;
  box-shadow: 0px 3px 8px gray;
  z-index: 5;
}
.positionList {
  display: inline-flex;
  width: 100vw;
  justify-content: space-evenly;
  padding-left: 0;
  font-family: Overpass;
  font-style: normal;
  font-weight: 900;
  font-size: 18px;
  line-height: 28px;
  margin-top: 19px;
}
.positionList li {
  width: 50px;
  height: 26px;
  padding-top: 3px;
}
.positionList li:hover {
  background-color: #4dd9ff;
  transition: all 0.2s cubic-bezier(0, 0, 0, 0);
  border-radius: 5px;
}
.positionList li:active {
  background-color: #4dd9ff;
  transition: all 0.1s cubic-bezier(0, 0, 0, 0);
  border-radius: 20px;
}
.bottomBar {
  position: fixed;
  bottom: 0px;
  height: 50px;
  width: 100vw;
  left: 0;
  text-align: center;
  background-color: #4dd9ff;
  box-shadow: 0px 3px 8px gray;
  z-index: 5;
}
.bottomList {
  display: inline-flex;
  width: 100vw;
  justify-content: space-evenly;
  padding-left: 0;
  font-family: Overpass;
  font-style: normal;
  font-weight: 900;
  font-size: 18px;
  line-height: 28px;
  margin-top: 7px;
}
.bottomList img {
  height: 38px;
  width: 38px;
}
.icon3 img {
  height: 55px;
  width: 55px;
  margin-top: -10px;
}
.all-players {
  width: 100vw;
  margin-top: 100px;
  background-color: white;
  padding-top: 30px;
  padding-bottom: 50px;
}
.playerHeadshots {
  width: 75px;
  height: 103px;
  border-radius: 30px;
  margin-top: -20px;
}
.playerRoutes {
  position: inherit;
}
.player-ranks {
  display: grid;
  grid-template-columns: 100%;
  grid-template-rows: 100%;
  gap: 0px 0px;
  grid-template-areas: "playerGrid";
  margin-bottom: -19px;
  width: 358px;
  height: 123px;
}

.playerGrid {
  display: grid;
  grid-template-columns: auto auto auto;
  grid-template-rows: auto auto auto;
  gap: 30px 0px;
  grid-template-areas:
    "playerImg playerName playerPosRank"
    "playerImg playerInfo playerPosRank"
    "playerImg playerProj playerPosRank";
  grid-area: playerGrid;
  width: 358px;
  /* width: 95vw; */
  height: 123px;
  /* border: 1px solid black; */
  margin: auto;
}

.playerImg {
  grid-area: playerImg;
  text-align: center;
  width: 121px;
  height: 121px;
}
.firstCircle {
  background-color: #4dd9ff;
  width: 92px;
  height: 92px;
  border-radius: 50px;
  margin: auto;
  -webkit-writing-mode: vertical-lr;
}
.secondCircle {
  background-color: white;
  width: 74px;
  height: 74px;
  border-radius: 50px;
  margin: auto;
  margin-left: 9px;
}

.playerName {
  grid-area: playerName;
  font-family: Overpass;
  font-style: normal;
  font-weight: 800;
  font-size: 16px;
  line-height: 25px;
  width: 186px;
}

.playerPosRank {
  grid-area: playerPosRank;
  font-family: Overpass;
  font-style: normal;
  font-weight: 900;
  font-size: 25px;
  line-height: 38px;
  text-align: center;
  width: 47px;
  height: 122px;
  z-index: 0;
}

.playerInfo {
  grid-area: playerInfo;
  font-family: Overpass;
  font-style: normal;
  font-weight: bold;
  font-size: 12px;
  line-height: 18px;
  width: 186px;
}

.playerProj {
  grid-area: playerProj;
  font-family: Overpass;
  font-style: normal;
  font-weight: bold;
  font-size: 12px;
  line-height: 18px;
  width: 186px;
}
.pName {
  height: 0px;
  margin-left: -10px;
}
.pRank {
  margin-left: -40px;
  margin-top: 35px;
  z-index: 0;
}
.pInfo {
  height: 0px;
  margin-left: -10px;
  margin-top: -35px;
}
.pProj {
  height: 0px;
  margin-left: -10px;
  margin-top: -56px;
}
.testName {
  margin-top: 50px;
}
#swipeToAdd {
  position: fixed;
  z-index: 100;
  color: white;
  background-color: black;
  text-align: center;
  width: 150px;
  height: 150px;
  margin: 30% 30% 30% 30%;
  opacity: 0%;
  display: none;
  border-radius: 10px;
}
.addedToWatchStar {
  position: absolute;
  /* padding: 10px;
  padding-right: 150px; */
  background-color: white;
  border: 2px solid rgb(233, 233, 233);
  border-radius: 15px;
  overflow-x: hidden;
  /* display: none; */
  visibility: hidden;
  /* margin-left: 275px;
    margin-top: -100px;
    margin-bottom: 25px; */
  /* width: 0px; */
  z-index: 4;
  right: -30px;
  margin-top: -92px;
  padding-right: 100px;
}
