a:hover {
  text-decoration: none;
}
.ml-1 {
  float: right;
  margin-top: 11px;
  border: 1px solid black;
  padding: 10px;
  border-radius: 15px;
  font-family: "AKZIDENZGROTESK-MEDIUM", Tahoma, Sans-Serif;
  color: black;
}

.box-date {
  background-color: black;
  margin: 2%;
  width: 95px;
  position: absolute;
  display: flex;
  flex-direction: column;
  text-align: center;
  color: white;
  border-radius: 15px;
  opacity: 0.91;
  height: 24px;
}
.box-photo {
  display: flex;
  width: 100%;
  display: flex;
  position: relative;
}
.jour {
  margin-top: -20px;
}

.postcard {
  flex-wrap: wrap;
  display: flex;
  border-radius: 10px;
  margin: 0 0 1rem 0;
  overflow: hidden;
  position: relative;
  box-shadow: 0 0.2rem 0.2rem grey;
  /* border: 4px solid #40a3f6; */
  border: 2px solid black;
  margin-right: 10px;
  margin-left: 10px;
}
.postcard:hover {
  background-color: #f6f6f6;
  transition: all 0.2s cubic-bezier(0, 0, 0, 0);
}
.postcard .postcard__img_link {
  display: contents;
}
.postcard a {
  color: inherit;
}
.postcard h1,
.postcard .h1 {
  margin-bottom: 0.5rem;
  font-weight: 500;
  line-height: 1.2;
}
.postcard .postcard__title {
  font-size: 1.75rem;
  transition: 0.2s ease-in-out color;
  margin-top: -20px;
  font-family: "AKZIDENZGROTESK-BOLD_0", Tahoma, Sans-Serif;
}
.postcard .postcard__title:hover {
  color: #40a3f6;
}
.postcard .postcard__img {
  max-height: 180px;
  width: 100%;
  object-fit: cover;
  position: relative;
}
.postcard .postcard__text {
  padding: 1.5rem;
  position: relative;
  display: flex;
  flex-direction: column;
  font-family: "AKZIDENZGROTESK-MEDIUM", Tahoma, Sans-Serif;
}
.postcard .postcard__preview-txt {
  overflow: hidden;
  text-overflow: ellipsis;
  text-align: justify;
  height: 100%;
}
.postcard .postcard__preview-txt p {
  font-size: 1.1rem;
}
.postcard .postcard__preview-txt a {
  transition: 0.2s ease-in-out color;
}
.postcard .postcard__preview-txt a:hover {
  color: #40a3f6;
}

@media screen and (min-width: 769px) {
  .postcard {
    flex-wrap: inherit;
  }
  .postcard .postcard__title {
    font-size: 2rem;
  }
  .postcard .postcard__img {
    max-width: 300px;
    max-height: 100%;
    transition: transform 0.3s ease;
  }
  .postcard .postcard__text {
    padding: 2rem;
    width: 100%;
  }
}
@media screen and (min-width: 1024px) {
  .postcard .postcard__text {
    padding: 2rem 3.5rem;
  }
  .postcard .postcard__img {
    max-width: 400px;
    max-height: 100%;
    transition: transform 0.3s ease;
  }
}

