#emailInput {
  width: 571px;
  height: 56px;
  left: 86px;
  /* identical to box height */
  padding: 0px;
  border: unset;
  /* color: #DCDCDC; */
  border: 2px solid #4dd9ff;
  padding-left: 6px;

  z-index: 2;
}
::-webkit-input-placeholder {
  /* Chrome/Opera/Safari */
  padding-left: 14px;
}
::-moz-placeholder {
  /* Firefox 19+ */
  padding-left: 14px;
}
:-ms-input-placeholder {
  /* IE 10+ */
  padding-left: 14px;
}
:-moz-placeholder {
  /* Firefox 18- */
  padding-left: 14px;
}
#emailInput::-webkit-input-placeholder {
  padding-left: 14px;
}
#submitEmailForm {
  position: relative;
  left: 50px;
  top: 55px;

  z-index: 2;
}
input:focus,
button:focus,
select:focus {
