}
#submitEmail {
  margin-top: 15px;
  width: 580px;
  height: 58px;
  background-color: #4dd9ff;
  font-family: Open Sans;
  font-style: normal;
  font-weight: bold;
  font-size: 22px;
  line-height: 30px;
