.comparison-page {
  width: 100vw;
  margin-top: 35px;
  background-color: white;
  padding-top: 30px;
  padding-bottom: 100px;
}
.compare-all-players {
  display: grid;
  grid-template-columns: auto 50px auto;
  grid-template-rows: auto auto auto auto;
  gap: 0px 0px;
  grid-template-areas:
    "player1 compare-info player2"
    "compare-trends-projections compare-trends-projections compare-trends-projections"
    "compare-attempts compare-attempts compare-attempts"
    "compare-felix-choice compare-felix-choice compare-felix-choice";
  width: 358px;
  margin: auto;
}

.player1 {
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: auto auto auto auto auto;
  gap: 0px 0px;
  grid-template-areas:
    "player1-image"
    "player1-Info"
    "player1-pos-info"
    "player1-projection-chart"
    "player1-outlook";
  grid-area: player1;
}

.player1-image {
  grid-area: player1-image;
  text-align: center;
  width: 121px;
  height: 121px;
}
.player1-image-img {
  width: 75px;
  height: 103px;
  border-radius: 30px;
  margin-top: -20px;
}

.player1-Info {
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: auto auto auto;
  gap: 0px 0px;
  grid-template-areas:
    "player1-name"
    "player1-team"
    "player1-status";
  grid-area: player1-Info;
  margin-top: -30px;
}

.player1-status {
  grid-area: player1-status;
  font-family: Overpass;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 21px;
  color: #000000;
}

.player1-team {
  grid-area: player1-team;
  font-family: Overpass;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 21px;
  color: #000000;
}

.player1-name {
  grid-area: player1-name;
  font-family: Overpass;
  font-style: normal;
  font-weight: 800;
  font-size: 16px;
  line-height: 28px;
  /* white-space: nowrap; */
  color: #000000;
}

.player1-pos-info {
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: auto auto;
  gap: 0px 0px;
  grid-template-areas:
    "player1-pos-rank"
    "player1-pos-rank-title";
  grid-area: player1-pos-info;
}
.player1-pos-rank {
  grid-area: player1-pos-rank;
  font-family: Overpass;
  font-style: normal;
  font-weight: 900;
  font-size: 25px;
  line-height: 38px;
  text-align: end;
  margin-bottom: -10px;
}
.player1-pos-rank-title {
  grid-area: player1-pos-rank-title;
  font-family: Overpass;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 21px;
  text-align: end;
}
.player1-projection-chart {
  grid-area: player1-projection-chart;
}

.player1-outlook {
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: auto auto;
  gap: 0px 0px;
  grid-template-areas:
    "player1-outlook-title"
    "player1-outlook-description";
  grid-area: player1-outlook;
}

.player1-outlook-title {
  grid-area: player1-outlook-title;
  font-family: Overpass;
  font-style: normal;
  font-weight: 800;
  font-size: 14px;
  line-height: 28px;

  color: #000000;
  text-align: end;
}

.player1-outlook-description {
  grid-area: player1-outlook-description;
  font-family: Overpass Mono;
  font-style: normal;
  font-weight: bold;
  font-size: 10px;
  line-height: 15px;
  text-align: end;
  color: #000000;
}

.player2 {
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: auto auto auto auto auto;
