  width: 92px;
  height: 92px;
  border-radius: 50px;
  margin: auto;
  -webkit-writing-mode: vertical-lr;
}
.secondCircle2 {
  background-color: white;
  width: 74px;
  height: 74px;
  border-radius: 50px;
  margin: auto;
  margin-left: 9px;
}
