  line-height: 15px;
  text-align: start;
  color: #000000;
}

.compare-info {
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: 50px 50px 118px 300px 50px;
  gap: 0px 0px;
  grid-template-areas:
    "middle-chat-button"
    "middle-info-empty"
    "middle-pos-info-empty"
    "middle-projection-charts"
    "middle-player-outlooks";
  grid-area: compare-info;
  width: 50px;
  justify-content: center;
}

.middle-chat-button {
  grid-area: middle-chat-button;
  margin-top: 80px;
  align-self: center;
  text-align: center;
}
.chatroomImageCompare {
  height: 45px;
}

.middle-info-empty {
  grid-area: middle-info-empty;
}

.middle-pos-info-empty {
  grid-area: middle-pos-info-empty;
}

.middle-projection-charts {
  grid-area: middle-projection-charts;
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: 34px 34px 34px 34px 34px 34px 34px;
  gap: 3.5px 0px;
  grid-template-areas:
    "center-rostered-title"
    "centered-att-title"
    "centered-yds-title"
    "centered-tds-title"
    "centered-season-total-title"
    "centered-avg-pts-title"
    "centered-proj-pts-title";
  font-family: Overpass;
  font-style: normal;
  font-weight: bold;
  font-size: 12px;
  text-align: center;

  width: 50px;
}
.center-rostered-title {
  grid-area: center-rostered-title;
  margin: auto;
}
.centered-att-title {
  grid-area: centered-att-title;
  margin: auto;
}
.centered-yds-title {
  grid-area: centered-yds-title;
  margin: auto;
}
.centered-tds-title {
  grid-area: centered-tds-title;
  margin: auto;
}
.centered-season-total-title {
  grid-area: centered-season-total-title;
  margin: auto;
}
.centered-avg-pts-title {
  grid-area: centered-avg-pts-title;
  margin: auto;
}
.centered-proj-pts-title {
  grid-area: centered-proj-pts-title;
  margin: auto;
}

.middle-player-outlooks {
  grid-area: middle-player-outlooks;
  background-color: rgb(226, 226, 226);
  width: 10px;
  justify-self: center;
  height: 265px;
  margin-top: -28px;
}

.compare-trends-projections {
  grid-area: compare-trends-projections;
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: auto auto;
  gap: 0px 0px;
  grid-template-areas:
    "compare-chart-title"
    "compare-chart-chart";
}
.compare-chart-title {
  grid-area: compare-chart-title;

  font-family: Overpass;
  font-style: normal;
  font-weight: 800;
  font-size: 18px;
  line-height: 28px;
  color: #000000;
  margin-top: 25px;
  margin-bottom: 15px;
}
.compare-chart-chart {
  grid-area: compare-chart-chart;
  border-left: 6px solid black;
  border-bottom: 6px solid black;
  margin-left: -5px;
  border-bottom-left-radius: 50px;
}

.compare-attempts {
  grid-area: compare-attempts;
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: auto auto;
  gap: 0px 0px;
  grid-template-areas:
    "compare-att-chart-title"
    "compare-att-chart-chart";
}
.compare-att-chart-title {
  grid-area: compare-att-chart-title;
  font-family: Overpass;
  font-style: normal;
  font-weight: 800;
  font-size: 18px;
  line-height: 28px;
  color: #000000;
  margin-top: 25px;
  margin-bottom: 15px;
}
.compare-att-chart-chart {
  grid-area: compare-att-chart-chart;
  border-left: 6px solid black;
  border-bottom: 6px solid black;
  margin-left: -5px;
  border-bottom-left-radius: 50px;
}

.compare-felix-choice {
  grid-area: compare-felix-choice;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr 1fr;
  gap: 0px 0px;
  grid-template-areas:
    "felix-choice-top"
    "felix-choice-bar";
  margin-top: 25px;
}
.felix-choice-top {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr;
  gap: 0px 0px;
  grid-template-areas: "felix-choice-player1-image felix-choice-title felix-choice-player2-image";
  grid-area: felix-choice-top;
}
.felix-choice-player1-image {
  grid-area: felix-choice-player1-image;
}
.felix-choice-player2-image {
  grid-area: felix-choice-player2-image;
}
.felix-choice-title {
  grid-area: felix-choice-title;
  align-self: center;
  font-family: Overpass;
  font-style: normal;
  font-weight: 900;
  font-size: 14px;
  text-align: center;
  color: #000000;
}
.felix-choice-bar {
  grid-area: felix-choice-bar;
}
